.maindivone {
  display: flex;
  background-color: #01172f;
  font-size: 20px;
  /* height: 100vh; */
  /* border: 5px solid rgb(197, 17, 17); */
  flex-direction: row;
  height: auto;
}

.map-container {
  width: 60vw;
  height: auto;
  /* border: solid 5px black; */
  /* border-radius: 20px; */
  border: 30px #fde293 solid;
}

.info-container {
  display: flex;
  flex-direction: column;
  background-color: #01172f;
  width: 40vw;
  height: 100vh;
  color: white;
  overflow-y: auto;
  /* border-radius: 20px; */
}

.cardSet {
  overflow-y: auto;
}

.info-container-header {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  background-color: #fde293;
  color: black;
  font-weight: bold;
}

.infoHeader {
  margin: 20px;
}

.bathroomCard {
  background-color: #247ba0;
  margin-top: 5px;
  border-radius: 50px;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 30px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.labelPosition {
  bottom: 30px;
  left: 0;
  position: relative;
}

.ratingBox {
  background-color: #247ba0;
  margin-top: 5px;
  border-radius: 50px;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 30px;
}

.filterBox {
  display: flex;
  background-color: #013f82;
  color: white;
  flex-direction: row;
  justify-content: space-between;
  justify-items: left;
  text-align: left;
  padding-left: 100px;
  padding-right: 100px;
}

@media (max-width: 767px) {
  .ratingBox {
    background-color: #247ba0;
    border-radius: 30px;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 30px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .ratingBox input {
    font-size: 20px;
    width: 50vw;
  }

  .ratingBox button {
    font-size: 15px;
    border: solid black 3px;
    border-radius: 20px;
    color: black;
  }

  .filterBox {
    display: flex;
    background-color: #013f82;
    color: white;
    flex-direction: column;
    justify-content: space-between;
    justify-items: left;
    text-align: center;
    font-size: 20px;
    padding-top: 5px;
    padding-bottom: 10px;
    /* padding-left: 30px;
    padding-right: 30px; */
  }

  .bathroomCard {
    background-color: #247ba0;
    margin-top: 5px;
    border-radius: 30px;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 15px;
  }

  select {
    color: black;
    border: solid 2px black;
  }

  .checkBox {
    /* box-shadow: black 5px 5px 5px; */
  }

  .maindivone {
    display: flex;
    background-color: rgb(30, 98, 56);
    font-size: 20px;
    height: 100vh;
    flex-direction: column;
  }

  .map-container {
    width: auto;
    height: 70vh;
    border: solid #fde293 5px;
  }

  .info-container {
    display: flex;
    flex-direction: column;
    background-color: #01172f;
    width: 100%;
    /* height: 100vh; */
    /* border: 5px solid rgb(31, 8, 8); */
    color: white;
    overflow-y: auto;
  }
}
